  function getFaviconEl() {
    return document.getElementById("favicon")
  }

  async function pingServers() {
    let serversList = JSON.parse(sessionStorage.getItem("serversList") ?? "[]")
    await Promise.all(
      serversList?.map(async (url) => {
        try {
          const response = await fetch(url, { method: "HEAD" })
          // console.log(lo)
          return response.ok
        } catch (error) {
          console.error(error)
          // return false
        }
      })
    )
  }

  useEffect(() => {
    if (isUserLoggedIn() !== null && window.location.pathname !== "/error") {
      const timeToFetch = 15 //in seconds
      let timer = null

      const handleVisibilityChange = () => {
        const datetime = new Date()
        if (document.hidden) {
          localStorage.setItem("inactive", Date.parse(datetime) / 1000)
          clearInterval(timer)
        } else {
          const lastTimeStr = localStorage.getItem("inactive")
          const timestamp = parseInt(lastTimeStr, 10)
          const lastTime = new Date(timestamp * 1000)
          const diffInSeconds =
            Math.abs(datetime.getTime() - lastTime.getTime()) / 1000
          if (diffInSeconds >= timeToFetch) {
            console.log(
              `Fetching data after ${diffInSeconds} seconds of inactivity`
            )
            store
              .dispatch(getMyNotifications({ unread_count: 1 }))
              .then((res) => {
                if (res?.payload) {
                  const count = res?.payload[0]?.count_unread ?? 0
                  if (count) {
                    const countFix =
                      count > 0 && count <= 20 ? `(${count})` : "(20+)"
                    document.title = `${countFix} ${document.title.replaceAll(
                      countFix,
                      ""
                    )}`
                    pingServers()
                  }
                }
              })
          } else {
            timer = setInterval(() => {
              const diffInSeconds =
                Math.abs(datetime.getTime() - lastTime.getTime()) / 1000
              if (diffInSeconds >= timeToFetch) {
                console.log(
                  `Fetching data after ${diffInSeconds} seconds of inactivity`
                )
                store
                  .dispatch(getMyNotifications({ unread_count: 1 }))
                  .then((res) => {
                    if (res?.payload) {
                      const count = res?.payload[0]?.count_unread ?? 0
                      if (count) {
                        const countFix =
                          count > 0 && count <= 20 ? `(${count})` : "(20+)"
                        document.title = `${countFix} ${document.title.replaceAll(
                          countFix,
                          ""
                        )}`
                      }
                      pingServers()
                    }
                  })
                clearInterval(timer)
              }
            }, 1000)
          }
        }
      }

      document.addEventListener("visibilitychange", handleVisibilityChange)

      return () => {
        clearInterval(timer)
        document.removeEventListener("visibilitychange", handleVisibilityChange)
      }
    }
  }, [])

  function hexToRgb(hex) {
    // Hilangkan tanda '#' jika ada
    hex = hex.replace("#", "")

    // Ubah nilai warna dari format hex menjadi format RGB
    const r = parseInt(hex.substring(0, 2), 16)
    const g = parseInt(hex.substring(2, 4), 16)
    const b = parseInt(hex.substring(4, 6), 16)

    // Kembalikan nilai warna dalam format RGB
    return `rgb(${r}, ${g}, ${b})`
  }

  function hexToRgbVal(hex) {
    // Hilangkan tanda '#' jika ada
    hex = hex.replace("#", "")

    // Ubah nilai warna dari format hex menjadi format RGB
    const r = parseInt(hex.substring(0, 2), 16)
    const g = parseInt(hex.substring(2, 4), 16)
    const b = parseInt(hex.substring(4, 6), 16)

    // Kembalikan nilai warna dalam format RGB
    return `${r}, ${g}, ${b}`
  }

  function darkenLightenColor(color, percent, asValue) {
    // Memeriksa apakah nilai yang diberikan adalah format hex
    let isHex = /^#([0-9a-f]{3}){1,2}$/i.test(color)

    // Konversi nilai hex ke dalam bentuk RGB
    if (isHex) {
      color = color.substring(1) // Hilangkan tanda '#'
      const hexToRgb = (hex) => {
        const bigint = parseInt(hex, 16)
        const r = (bigint >> 16) & 255
        const g = (bigint >> 8) & 255
        const b = bigint & 255
        return [r, g, b]
      }
      const rgb = hexToRgb(color)
      color = `rgb(${rgb.join(", ")})`
    }

    // Memisahkan nilai RGB
    const values = color.match(/\d+/g)
    let r = parseInt(values[0])
    let g = parseInt(values[1])
    let b = parseInt(values[2])

    // Menerapkan penurunan kecerahan (darken) atau peningkatan kecerahan (lighten)
    r = Math.floor((r * (100 - percent)) / 100)
    g = Math.floor((g * (100 - percent)) / 100)
    b = Math.floor((b * (100 - percent)) / 100)

    if (asValue) {
      return `${r}, ${g}, ${b}`
    }
    return `rgb(${r}, ${g}, ${b})`
  }

