    //     .replace(/#3772be/g, `${primaryColorBe}`)
    //     .replace(/#3e7fd5/g, `${primaryColorBe}`)
    //     .replace(/#346bb3/g, `${primaryColorBe}`)
    //     .replace(/rgb\(65,\s*134,\s*224/g, `${rgbColorReplacer}`)
